export default {
  name: "my-wish-list",
  data(){
      return{
        src:''
      }
  },
  computed:{
    styles(){
      let obj ={ left: "50%"}
      if(this.$route.name === 'toberealized' ){
        obj ={ left: 0 }
        return obj
      }
      return obj
    }
  },
  mounted(){
   document.title="我的心愿单"
  },
  watch:{
      '$route'(){
       // if(this.$route.name === 'toberealized' ){
       //   this.styles.left = 0
       // }
       // else{
       //   this.styles.left = '50%'
       // }
      }
  },
  created(){

  },
  methods:{
    qh(p){
      this.$router.replace(p)
    },
    readURL(){
      let input = this.$refs.files ,vm = this;
      if (input.files && input.files[0]) {
        var reader = new FileReader();
        reader.onload = function (e) {
          vm.src =  e.target.result
          console.log( vm.src )
        }
        reader.readAsDataURL(input.files[0]);
      }
    }
  }
}



